import React from 'react';
import { render, screen, fireEvent, waitFor, act } from '@testing-library/react';
import BirthdayPianoSurprise from '../../App';

// Mock the media loader
jest.mock('../../utils/mediaLoader', () => ({
  loadMediaFiles: jest.fn(() => Promise.resolve({
    pictures: [
      { id: 1, url: '/media/pictures/test1.jpg', alt: 'Memory 1', filename: 'test1.jpg' },
      { id: 2, url: '/media/pictures/test2.jpg', alt: 'Memory 2', filename: 'test2.jpg' }
    ],
    videos: [
      { id: 1, url: '/media/videos/test.mp4', alt: 'Video 1', filename: 'test.mp4' }
    ]
  }))
}));

// Mock the celebration store
jest.mock('../../store/celebrationStore', () => ({
  __esModule: true,
  default: () => ({
    setName: jest.fn(),
    setMessage: jest.fn()
  })
}));

describe('BirthdayPianoSurprise App', () => {
  beforeEach(() => {
    jest.clearAllMocks();
    jest.useFakeTimers();
  });

  afterEach(() => {
    jest.runOnlyPendingTimers();
    jest.useRealTimers();
  });

  describe('Initial Render', () => {
    it('should render welcome screen initially', () => {
      render(<BirthdayPianoSurprise />);
      
      expect(screen.getByText('ðŸŽ‰ HAPPY BIRTHDAY! ðŸŽ‰')).toBeInTheDocument();
      expect(screen.getByText('To My Beautiful Wifey')).toBeInTheDocument();
    });

    it('should load media files on mount', async () => {
      const { loadMediaFiles } = require('../../utils/mediaLoader');
      
      render(<BirthdayPianoSurprise />);
      
      await waitFor(() => {
        expect(loadMediaFiles).toHaveBeenCalledTimes(1);
      });
    });

    it('should initialize with correct default state', () => {
      render(<BirthdayPianoSurprise />);
      
      // Should show welcome screen, not game or celebration
      expect(screen.queryByText('Score:')).not.toBeInTheDocument();
      expect(screen.queryByText('Lives:')).not.toBeInTheDocument();
    });
  });

  describe('Game Flow', () => {
    it('should start game when start button is clicked', async () => {
      render(<BirthdayPianoSurprise />);
      
      const startButton = screen.getByText('ðŸŽµ Start Playing');
      
      await act(async () => {
        fireEvent.click(startButton);
      });
      
      // Should show game UI
      await waitFor(() => {
        expect(screen.getByText(/Score:/)).toBeInTheDocument();
        expect(screen.getByText(/Lives:/)).toBeInTheDocument();
      });
    });

    it('should show mute button in game', async () => {
      render(<BirthdayPianoSurprise />);
      
      const startButton = screen.getByText('ðŸŽµ Start Playing');
      
      await act(async () => {
        fireEvent.click(startButton);
      });
      
      await waitFor(() => {
        expect(screen.getByRole('button', { name: /ðŸ”Š|ðŸ”‡/ })).toBeInTheDocument();
      });
    });
  });

  describe('Media Galleries', () => {
    it('should show photo gallery button after game completion', async () => {
      render(<BirthdayPianoSurprise />);
      
      // Start and complete game (simplified)
      const startButton = screen.getByText('ðŸŽµ Start Playing');
      await act(async () => {
        fireEvent.click(startButton);
      });
      
      // Fast forward to trigger celebration
      act(() => {
        jest.advanceTimersByTime(5000);
      });
      
      await waitFor(() => {
        expect(screen.getByText('Photo Gallery')).toBeInTheDocument();
      });
    });

    it('should show video gallery button after game completion', async () => {
      render(<BirthdayPianoSurprise />);
      
      // Start and complete game (simplified)
      const startButton = screen.getByText('ðŸŽµ Start Playing');
      await act(async () => {
        fireEvent.click(startButton);
      });
      
      // Fast forward to trigger celebration
      act(() => {
        jest.advanceTimersByTime(5000);
      });
      
      await waitFor(() => {
        expect(screen.getByText('Video Gallery')).toBeInTheDocument();
      });
    });

    it('should open photo gallery modal when clicked', async () => {
      render(<BirthdayPianoSurprise />);
      
      // Navigate to celebration screen
      const startButton = screen.getByText('ðŸŽµ Start Playing');
      await act(async () => {
        fireEvent.click(startButton);
      });
      
      act(() => {
        jest.advanceTimersByTime(5000);
      });
      
      await waitFor(() => {
        const photoGalleryButton = screen.getByText('Photo Gallery');
        fireEvent.click(photoGalleryButton);
      });
      
      // Should show photo gallery modal
      await waitFor(() => {
        expect(screen.getByText('ðŸ“¸')).toBeInTheDocument();
      });
    });
  });

  describe('Audio Controls', () => {
    it('should toggle mute state when mute button is clicked', async () => {
      render(<BirthdayPianoSurprise />);
      
      // Start game to show mute button
      const startButton = screen.getByText('ðŸŽµ Start Playing');
      await act(async () => {
        fireEvent.click(startButton);
      });
      
      await waitFor(() => {
        const muteButton = screen.getByRole('button', { name: /ðŸ”Š|ðŸ”‡/ });
        fireEvent.click(muteButton);
      });
      
      // Button should change state (implementation detail)
      expect(screen.getByRole('button', { name: /ðŸ”Š|ðŸ”‡/ })).toBeInTheDocument();
    });
  });

  describe('Responsive Design', () => {
    it('should handle mobile viewport', () => {
      // Mock mobile viewport
      Object.defineProperty(window, 'innerWidth', {
        writable: true,
        configurable: true,
        value: 375,
      });
      
      render(<BirthdayPianoSurprise />);
      
      // Should render without errors on mobile
      expect(screen.getByText('ðŸŽ‰ HAPPY BIRTHDAY! ðŸŽ‰')).toBeInTheDocument();
    });

    it('should handle desktop viewport', () => {
      // Mock desktop viewport
      Object.defineProperty(window, 'innerWidth', {
        writable: true,
        configurable: true,
        value: 1920,
      });
      
      render(<BirthdayPianoSurprise />);
      
      // Should render without errors on desktop
      expect(screen.getByText('ðŸŽ‰ HAPPY BIRTHDAY! ðŸŽ‰')).toBeInTheDocument();
    });
  });

  describe('Error Handling', () => {
    it('should handle media loading errors gracefully', async () => {
      const { loadMediaFiles } = require('../../utils/mediaLoader');
      loadMediaFiles.mockRejectedValueOnce(new Error('Network error'));
      
      const consoleSpy = jest.spyOn(console, 'error').mockImplementation();
      
      render(<BirthdayPianoSurprise />);
      
      await waitFor(() => {
        expect(consoleSpy).toHaveBeenCalledWith('Error loading media:', expect.any(Error));
      });
      
      consoleSpy.mockRestore();
    });

    it('should continue working even with audio context errors', () => {
      // Mock AudioContext to throw error
      global.AudioContext = jest.fn(() => {
        throw new Error('AudioContext not supported');
      });
      
      expect(() => render(<BirthdayPianoSurprise />)).not.toThrow();
      
      // Should still show welcome screen
      expect(screen.getByText('ðŸŽ‰ HAPPY BIRTHDAY! ðŸŽ‰')).toBeInTheDocument();
    });
  });
});
